<template>
  <div class="menu-box">
    <div class="menu-logo">
      <img src="https://img1.gamersky.com/image2020/09/20200908_ls_red_141_2/gamersky_01small_02_2020981428F89.jpg">
    </div>
    <div class="menu-nav">
      <ul>
        <li v-for="(menu, index) in menuList"
            :class="index === menuActivation ? 'menu-nav-activation': ''"
            @click="clickMenuItem(index)"
            :key="menu.id">
          <a>{{ menu.name }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MenuBar',
  data () {
    return {
      menuActivation: 0,
      menuList: [
        {'id': 1, 'name': '首页-1'},
        {'id': 2, 'name': '首页-2'},
        {'id': 3, 'name': '首页-3'},
        {'id': 4, 'name': '首页-4'},
        {'id': 5, 'name': '首页-5'}
      ]
    }
  },
  methods: {
    clickMenuItem (index) {
      this.menuActivation = index
    }
  }
}
</script>

<style scoped>
.menu-logo{
  width: 60px;
  height: 60px;
  border-radius: 5px;
  float: left;
  margin-top: 20px;
  margin-left: 20%;
}
.menu-logo>img{
  width: 100%;
  height: 100%;
  border-radius: 5px;
}
.menu-nav{
  width: auto;
  height: 30px;
  float: left;
  margin-top: 35px;
  margin-left: 30%;
}
.menu-nav>ul>li{
  float: left;
  padding: 3px 30px;
  margin-left: 10px;
  border-radius: 8px;
  border: #fff solid 2px;
  transition: .7s;
}
.menu-nav>ul>li>a{
  line-height: 30px;
  font-size: 16px;
}
.menu-nav-activation{
  border-top-color: #a5673f !important;
  border-bottom-color: #6739b6 !important;
  border-left-color: #9c26b0 !important;
  border-right-color: #e03997 !important;
}
.menu-nav>ul>li:hover{
  border-top-color: #1890ff;
  border-bottom-color: #ff4d4f;
  border-left-color: #f37b1d;
  border-right-color: #fbbd08;
}
</style>



