<template>
  <div id="components-layout-demo-basic">
    <div id="return">xx</div>
    <a-layout>
      <a-layout-header>
        <MenuBar/>
      </a-layout-header>
      <a-layout-content>
        <div id="content-box">
          <!-- /大图和子标题 -->
          <div>
            <!--
              http://imgboys1.yohobuy.com/spidercms/2018/01/16/01/01362b8da1df42a76d442d81454ddab979.jpeg
              http://imgboys1.yohobuy.com/spidercms/2018/01/16/01/019e17835bd308e0b994d7da60d3c49f04.jpeg
              http://imgboys1.yohobuy.com/spidercms/2018/01/16/01/01448ca23d42c17405b180814c223d6074.jpeg
              http://imgboys1.yohobuy.com/spidercms/2018/01/16/01/0130a16fd08dbdd65cfc7f21ff7a5016f6.jpeg
              https://twgreatdaily.com/images/elastic/EwB/EwBLM3EBfwtFQPkd9d3y.jpg
              https://twgreatdaily.com/images/elastic/DQB/DQBLM3EBfwtFQPkd9N15.jpg
            -->
            <div class="content-big-img">
              <img :src="bigImgSrc"/>
            </div>
            <div class="content-sub-title-img">
              <ul>
                <li v-for="(subTitle, index) in subTitleList" :key="index">
                  <div class="sub-title">
                    <img :src="subTitle.src"/>
                    <div>
                      <div>
                        <span>{{ subTitle.name }}</span>
                        <p>{{ subTitle.desc }}</p>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <!-- /大图和子标题 -->
          <!-- 内容栏 -->
          <div class="content-text-box">
            <div><LeftContentBar/></div>
            <div><RightContentBar/></div>
          </div>
          <!-- /内容栏 -->
        </div>
      </a-layout-content>
      <a-layout-footer><FooterBar/></a-layout-footer>
    </a-layout>
  </div>
</template>

<script>
import MenuBar from '../components/menu/MenuBar'
import LeftContentBar from '../components/content/left/LeftContentBar'
import RightContentBar from '../components/content/right/RightContentBar'
import FooterBar from '../components/footer/FooterBar'
export default {
  name: 'Index',
  data () {
    return {
      bigImgSrc: 'http://img02.yohoboys.com/contentimg/2018/01/16/11/026016f33d745dd9027af5d9083a379f2e.png',
      subTitleList: [
        {'id': 1, 'src': 'https://p3.pstatp.com/large/5e700000659c83c0a844', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'},
        {'id': 2, 'src': 'https://p3.pstatp.com/large/5b5c00020d012c310249', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'},
        {'id': 3, 'src': 'https://5b0988e595225.cdn.sohucs.com/images/20171124/d8cf4326203b41f592df25d9d326e9fa.jpeg', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'},
        {'id': 4, 'src': 'https://lh3.googleusercontent.com/proxy/WPC6VrFvwT_ls4Kz8I1_zYg7HIz7pfDDFT800wkd_IiGLIFNywF6lZUxpi58sHXOCyD6YJYjaxvZVpnDSoxZ_LmtJ9-HLBszx_ZHncjBamt9NJ4CmbWCeA', 'name': '吉冈里帆', 'desc': '1993年1月15日出生于日本京都府，影视演员'}
      ]
    }
  },
  components: {
    MenuBar,
    LeftContentBar,
    RightContentBar,
    FooterBar
  }
}
</script>

<style scoped>
#return{
  position: fixed;
  width: 100px;
  height: 100px;
  border: #1890ff solid 1px;
  right: 50px;
  bottom: 100px;
}
#components-layout-demo-basic .ant-layout-header,
#components-layout-demo-basic .ant-layout-footer {
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
  height: 100px;
  line-height: normal;
}
#components-layout-demo-basic .ant-layout-content {
  background: #fff;
}
#content-box{
  width: 76%;
  height: auto;
  margin: 10px auto;
}
.content-big-img>img{
  width: 100%;
  height: 500px;
}
.content-sub-title-img{
  height: 210px;
}
.content-sub-title-img>ul>li{
  float: left;
  width: 25%;
  padding: 17px 11px 10px;
  cursor: pointer;
}
.content-sub-title-img>ul>li .sub-title{
  position: relative;
}
.sub-title>img{
  position: absolute;
  width: 100%;
  height: 180px;
  border-radius: 5px;
}
.sub-title>div{
  position: absolute;
  width: 100%;
  height: 180px;
  background: rgba(0, 0, 0, 0);
  color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  text-align: center;
  transition: .5s;
}
.sub-title>div>div{
  width: 70%;
  margin: 20% auto;
}
.sub-title>div>div>span{
  padding: 1px 6px;
  font-size: 18px;
}
.sub-title>div>div>p{
  font-size: 14px;
}
.sub-title>div:hover{
  background: rgba(0, 0, 0, .5);
  color: #fff;
  border-color: #fff;
}
.content-text-box>div{
  float: left;
}
.content-text-box>div:first-child{
  width: 70%;
}
.content-text-box>div:last-child{
  width: 29%;
  margin-left: 1%;
}
.ant-layout{
  background: #fff;
}
#components-layout-demo-basic .ant-layout-footer {
  background: #000;
  margin-top: 200px;
  height: 500px;
}
</style>



